export default {
	onLoad (q) {
		console.log(q)
		this.formData.caseId = parseInt(q.id)
	},
	data () {
		return {
			// 合同
			attachment: '',
			formData: {
				caseId: '',
				productName: '',
				investChannel: '',
				investMoney: '',
				investAddress: '',
				guaranteeAmount: '',
				caseRefund: '',
				caseNotRefund: '',
				remark: ''
			},
			// 购买渠道
		}
	},
	methods: {
		channelChange (evt) {
			console.log(evt)
			this.formData.investChannel = evt.detail.value
		},
		enter () {
			this.$JRequest({
				method: 'FILE',
				url: 'caseChannel',
				name: 'attachment',
				filePath: this.attachment[0],
				files: this.attachment.map(v => ({
					name: 'attachment',
					uri: v
				})),
				formData: this.formData,
				success: res => {
					console.log(res)
				}
			})
			// this.$JRequest('caseChannel', this.formData, data => {
			// 	this.$JFn.showSuccess('举证成功')
			// })
		}
	}
}
